<template>
  <div class="fit-parent">
    <div class="banner-picture-div jquery-scrollbar-element scrollbar-macosx">
      <div class="padding24">
        <div class="title-div">
          <h2 class="title-text">
            <span>轮播图管理</span>
          </h2>
        </div>
        <div class="accounts-div">
          <div class="tools-div">
            <el-form :inline="true" class="demo-form-inline">
              <el-form-item label="工具栏：">
                <el-button
                  size="small"
                  plain
                  @click="dialog.addPictureVisible=true"
                  icon="el-icon-plus"
                >添加图片</el-button>
              </el-form-item>
            </el-form>
          </div>
          <div class="account-table">
            <el-tabs type="border-card" v-model="activeTab" @tab-click="updateInfo">
              <el-tab-pane label="图片列表" name="menuList">
                <i class="el-icon-refresh refresh-table" @click="getTableInfo()"></i>
                <div class="table-div jquery-scrollbar-element scrollbar-macosx">
                  <div class="inline-block">
                    <el-table :data="tableInfo.tableData" border style="width: 100%">
                      <el-table-column label="图片 ID" prop="picId"></el-table-column>
                      <el-table-column label="图片">
                        <template slot-scope="scope">
                          <el-image style="width: 160px; height: 75px" fit="cover" :src="scope.row['picUrl']"></el-image>
                        </template>
                      </el-table-column>
                      <el-table-column label="备注" prop="picMessage"></el-table-column>
                      <el-table-column label="创建时间" prop="picCreate"></el-table-column>
                      <el-table-column label="操作" width="100">
                        <template slot-scope="scope">
                          <el-button
                            type="primary"
                            icon="el-icon-edit"
                            circle
                            title="编辑"
                            @click="showEditPicture(scope.$index)"
                            size="small"
                          ></el-button>
                          <el-button
                            type="danger"
                            icon="el-icon-delete"
                            circle
                            title="删除图片"
                            @click="deletePicture(scope.$index)"
                            size="small"
                          ></el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div>
                </div>
                <div class="pager-div">
                  <el-pagination
                    @size-change="changePageSize"
                    @current-change="changePage"
                    :current-page="tableInfo.page_num"
                    :page-sizes="[3,4,6, 8, 10, 15, 20, 30]"
                    :page-size="tableInfo.page_size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="tableInfo.total_num"
                    :pager-count="5"
                    size="small"
                  ></el-pagination>
                </div>
              </el-tab-pane>
              <!-- <el-tab-pane label="菜单预览" name="menuPreview">菜单预览</el-tab-pane> -->
              <el-tab-pane label="图片预览" name="picturePreview">
                <div class="picture-preview-div">
                  <el-carousel trigger="click" height="300px">
                    <el-carousel-item v-for="(value) in tableInfo.tableData" :key="value.picId">
                      <el-image style="width: 640px; height: 300px" fit="fill" :src="value['picUrl']"></el-image>
                    </el-carousel-item>
                  </el-carousel>
                </div>
              </el-tab-pane>
            </el-tabs>
          </div>
        </div>
        <el-dialog title="添加图片" :visible.sync="dialog.addPictureVisible" :modal-append-to-body="false">
          <el-form :model="dialog.newPicture" :rules="dialog.rules" ref="addPictureForm">
            <el-form-item label="图片" :label-width="'120px'" prop="productIcon">
              <el-upload class="avatar-uploader" :on-success="uploadIconSuccess" :action="uploadUrl" :limit="1"
              name="file" :before-upload="beforeIconUpload" 
              :on-exceed="uploadExcced">
                <img v-if="dialog.newPicture.picUrl" :src="dialog.newPicture.picUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="备注" :label-width="'120px'" prop="phone">
              <el-input v-model="dialog.newPicture.picMessage" placeholder="请输入备注" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog.addPictureVisible = false">取 消</el-button>
            <el-button type="primary" @click="addPicture()">确 定</el-button>
          </div>
        </el-dialog>
        <el-dialog title="编辑图片" :visible.sync="dialog.editPcitureVisible" :modal-append-to-body="false">
          <el-form :model="dialog.picture" :rules="dialog.rules" ref="editPictureForm">
            <el-form-item label="图片" :label-width="'120px'" prop="productIcon">
              <el-upload class="avatar-uploader" :on-success="editIconSuccess" :action="uploadUrl" :limit="1"
              name="file" :before-upload="beforeIconUpload" :on-exceed="uploadExcced"
              :file-list="[{name: dialog.picture.picUrl.split('/').pop(),url:dialog.picture.picUrl}]">
                <img v-if="dialog.picture.picUrl" :src="dialog.picture.picUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
            <el-form-item label="备注" :label-width="'120px'" prop="phone">
              <el-input v-model="dialog.picture.picMessage" placeholder="请输入备注" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog.editPcitureVisible = false">取 消</el-button>
            <el-button type="primary" @click="editPicture()">确 定</el-button>
          </div>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "../common/http.js";
export default {
  data() {
    let data = {
      uploadUrl: '/seller/product/uploadIcon',
      dialog: {
        addPictureVisible: false,
        editPcitureVisible: false,
        newPicture: {
          picUrl: "",
          picMessage: ""
        },
        picture: {
          picUrl: "",
          picMessage: ""
        },
        rules: {
          picUrl: [
            {
              validator: (rule, value, callback) => {
                if (!value) {
                  callback(new Error("请上传图片"));
                } else {
                  callback();
                }
              },
              trigger: "change"
            }
          ]
        }
      },
      activeTab: "menuList",
      tableInfo: {
        page_num: 1,
        page_size: 4,
        total_num: 1,
        tableData: [
          /*{picId ...}*/
        ]
      }
    };
    return data;
  },
  mounted() {
    this.$(".jquery-scrollbar-element").scrollbar();
    this.getTableInfo();
  },
  methods: {
    changePage(page_num) {
      this.tableInfo.page_num = page_num;
      this.getTableInfo();
    },
    changePageSize(page_size) {
      this.tableInfo.page_size = page_size;
      this.getTableInfo();
    },
    updateInfo() {
      if (this.activeTab == "menuList") {
        this.getTableInfo();
      }
    },
    getTableInfo() {
      let tb = this.tableInfo;
      //获取列表信息
      axios
        .get("/picture/list", {
          page_num: tb.page_num,
          page_size: tb.page_size
        })
        .then(res => {
          if (res && res.code == 1 && res.data) {
            this.tableInfo = Object.assign(this.tableInfo, res.data);
          }
        });
    },
    //
    showaddPicture(){
      this.dialog.addPictureVisible = true;
    },
    showEditPicture(index) {
      let table = this.tableInfo;
      this.dialog.picture = {...table.tableData[index]};
      this.dialog.editPcitureVisible = true;

    },
    addPicture(){
      this.dialog.addPictureVisible = false;
      this.editPicture(true);
    },
    editPicture(isAdd=false){
      if(!isAdd) this.dialog.editPcitureVisible = false;
      let pic = isAdd ? this.dialog.newPicture : this.dialog.picture;
      let action = isAdd ? '添加' : '编辑';
      let ref = isAdd ? this.$refs["addPictureForm"] : this.$refs["editPictureForm"];
      let picture = {...pic};
      delete picture.updateTime;
      delete picture.picCreate;
      ref.validate(valid => {
        if(valid){
          axios.post('/picture/save',picture).then(res=>{
            if (res && res.code == 1) {
              this.$message({ message: action+"图片成功", type: "success" });
              if(isAdd){
                ref.resetFields();
              }
              this.updateInfo();
            } else {
              this.$alert(res.tips, action+"图片失败", {
                confirmButtonText: "确定"
              });
            }
          });
        } else {
          this.$alert("信息填写有误", "未提交", {
            confirmButtonText: "确定"
          });
          return false;
        }
      });
    },
    deletePicture(index) {
      let table = this.tableInfo;
      let picId = table.tableData[index].picId;
      this.$confirm('此操作将永久删除该图片, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios.post('/picture/delete',{picId}).then((res)=>{
          if(res&&res.code==1){
            this.$message({message: '删除图片成功',type: 'success'});
            this.updateInfo();
          }else{
            this.$alert(res.tips,"删除图片失败",{confirmButtonText: '确定'})
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    },
    uploadExcced(){
      this.$message({message:'仅支持单个图片上传，点击删除原图片后重新选择',type: 'warning'});
    },
    beforeIconUpload(file) {
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!');
      }
      return isLt2M;
    },
    uploadIconSuccess(res){
      if(res&&res.code==1){
        this.dialog.newPicture.picUrl = res.data;
      }
    },
    editIconSuccess(res){
      if(res&&res.code==1){
        this.dialog.picture.picUrl = res.data;
      }
    }
  }
};
</script>

