<template>
<div class="fit-parent">
  <div class="order-manage-div jquery-scrollbar-element scrollbar-macosx">
    <div class="padding24">
    <div class="title-div">
      <h2 class="title-text">
        <span>订单管理</span>
      </h2>
    </div>
    <div class="orders-div">
      <div class="tools-div">
        <el-form :inline="true" :model="searchInfo" class="demo-form-inline">
          <el-form-item>
            <el-select v-model="orderStatus" placeholder="请选择" size="small" style="width:100px">
              <el-option
                v-for="item in statusList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item>
            <el-input
              size="small"
              class="search-input"
              placeholder="请输入用户昵称/联系方式/订单编号"
              prefix-icon="el-icon-search"
              @keydown.enter.native="getSearchInfo()"
              v-model="keyword"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button
              size="small"
              plain
              type="primary"
              @click="getSearchInfo()"
              icon="el-icon-search"
            >查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="order-table">
        <el-tabs type="border-card" v-model="activeTab" @tab-click="updateInfo">
          <el-tab-pane label="订单列表" name="menuList">
            <i class="el-icon-refresh refresh-table" @click="getTableInfo()"></i>
            <div class="table-div jquery-scrollbar-element scrollbar-macosx">
              <div class="inline-block">
                <el-table :data="tableInfo.tableData" border style="width: 100%" @expand-change="updateScrollbar">
                  <el-table-column type="expand">
                    <template slot-scope="props">
                      <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item
                          v-for="(v,k) in tableInfo.stringMap"
                          :key="v.id"
                          :label="tableInfo.stringMap[k]"
                        >
                          <span>{{ props.row[k] }}</span>
                        </el-form-item>
                        <el-form-item :label="'订单内容'">
                          <div v-for="(v) in props.row.orderDetailList" :key="v.detailId">
                            {{v.productName}} &yen;{{v.productPrice}} x {{v.productQuantity}}
                          </div>
                        </el-form-item>
                      </el-form>
                    </template>
                  </el-table-column>
                  <el-table-column label="订单 ID" prop="orderId"></el-table-column>
                  <el-table-column label="用户名称" prop="buyerName"></el-table-column>
                  <el-table-column label="订单金额" prop="orderAmount"></el-table-column>
                  <el-table-column label="联系方式" prop="buyerPhone"></el-table-column>
                  <el-table-column label="订单状态" prop="orderStatusStr"></el-table-column>
                  <el-table-column label="操作" width="150">
                    <template slot-scope="scope">
                      <el-button
                        type="warning"
                        icon="el-icon-coin"
                        circle
                        title="确定接单/确定支付"
                        :disabled="scope.row.orderStatus != actionMap['new'].status"
                        @click="changeStatus(scope.$index,'pay')"
                        size="small"
                      ></el-button>
                      <el-button
                        type="success"
                        icon="el-icon-check"
                        circle
                        title="确定完结"
                        :disabled="[actionMap['new'].status,actionMap['pay'].status].indexOf(scope.row.orderStatus)<0"
                        @click="changeStatus(scope.$index,'over')"
                        size="small"
                      ></el-button>
                      <el-button
                        type="danger"
                        icon="el-icon-close"
                        circle
                        title="取消订单"
                        :disabled="[actionMap['new'].status,actionMap['pay'].status].indexOf(scope.row.orderStatus)<0"
                        @click="changeStatus(scope.$index,'cancel')"
                        size="small"
                      ></el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </div>
            <div class="pager-div">
              <el-pagination
                @size-change="changePageSize"
                @current-change="changePage"
                :current-page="tableInfo.page_num"
                :page-sizes="[6, 8, 10, 15, 20, 30]"
                :page-size="tableInfo.page_size"
                layout="total, sizes, prev, pager, next, jumper"
                :total="tableInfo.total_num"
                :pager-count="5"
                size="small"
              ></el-pagination>
            </div>
          </el-tab-pane>
          <!-- <el-tab-pane label="菜单预览" name="menuPreview">菜单预览</el-tab-pane> -->
          <el-tab-pane label="搜索结果" name="searchResult">
            <i class="el-icon-refresh refresh-table" @click="getSearchInfo(true)"></i>
            <div class="table-div jquery-scrollbar-element scrollbar-macosx">
              <div class="inline-block">
                <el-table :data="searchInfo.tableData" style="width: 100%" border @expand-change="updateScrollbar">
                  <el-table-column type="expand">
                    <template slot-scope="props">
                      <el-form label-position="left" inline class="demo-table-expand">
                        <el-form-item
                          v-for="(v,k) in tableInfo.stringMap"
                          :key="v.id"
                          :label="tableInfo.stringMap[k]"
                        >
                          <span>{{ props.row[k] }}</span>
                        </el-form-item>
                        <el-form-item :label="'订单内容'">
                          <div v-for="(v) in props.row.orderDetailList" :key="v.detailId">
                            {{v.productName}} &yen;{{v.productPrice}} x {{v.productQuantity}}
                          </div>
                        </el-form-item>
                      </el-form>
                    </template>
                  </el-table-column>
                  <el-table-column label="订单 ID" prop="orderId"></el-table-column>
                  <el-table-column label="用户名称" prop="buyerName"></el-table-column>
                  <el-table-column label="订单金额" prop="orderAmount"></el-table-column>
                  <el-table-column label="联系方式" prop="buyerPhone"></el-table-column>
                  <el-table-column label="订单状态" prop="orderStatusStr"></el-table-column>
                  <el-table-column label="操作" width="150">
                    <template slot-scope="scope">
                      <el-button
                        type="warning"
                        icon="el-icon-coin"
                        circle
                        title="确定接单/确定支付"
                        :disabled="scope.row.orderStatus != actionMap['new'].status"
                        @click="changeStatus(scope.$index,'pay')"
                        size="small"
                      ></el-button>
                      <el-button
                        type="success"
                        icon="el-icon-check"
                        circle
                        title="确定完结"
                        :disabled="[actionMap['new'].status,actionMap['pay'].status].indexOf(scope.row.orderStatus)<0"
                        @click="changeStatus(scope.$index,'over')"
                        size="small"
                      ></el-button>
                      <el-button
                        type="danger"
                        icon="el-icon-close"
                        circle
                        title="取消订单"
                        :disabled="[actionMap['new'].status,actionMap['pay'].status].indexOf(scope.row.orderStatus)<0"
                        @click="changeStatus(scope.$index,'cancel')"
                        size="small"
                      ></el-button>
                    </template>
                  </el-table-column>
                </el-table>
              </div>
            </div>
            <div class="pager-div">
              <el-pagination
                @size-change="changeSearchPageSize"
                @current-change="changeSearchPage"
                :current-page="searchInfo.page_num"
                :page-sizes="[6, 8, 10, 15, 20, 30]"
                :page-size="searchInfo.page_size"
                layout="total, sizes, prev, pager, next, jumper"
                :total="searchInfo.total_num"
                :pager-count="5"
                size="small"
              ></el-pagination>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
  </div>
</div>
</template>

<script>
import axios from "../common/http.js";
export default {
  data() {
    let data= {
      activeTab: "menuList",
      statusList: [
        { label: "全部", value: -1 },
        { label: "新订单未支付", value: 0 },
        { label: "订单已支付", value: 1 },
        { label: "已取消", value: 2 },
        { label: "已完结", value: 3 },
        { label: "已评价", value: 4 }
      ],
      actionMap: {
        //存储状态码
        'new': {
          status: 0
        },
        'pay': {
          url: '/pay/confirmPay',
          tips: '确认支付',
          status: 1
        },
        'over': {
          url: '/seller/order/finish',
          tips: '确认完结',
          status: 3
        },
        'cancel': {
          url: '/seller/order/cancel',
          tips: '订单取消',
          status: 2
        }
      },
      statusMap: {},
      searchInfo: {
        keyword: "",
        orderStatus: -1,
        page_num: 1,
        page_size: 8,
        total_num: 1,
        tableData: []
      },
      orderStatus: -1,
      keyword: "",
      tableInfo: {
        page_num: 1,
        page_size: 8,
        total_num: 1,
        stringMap: {
          orderId: "订单ID",
          buyerName: "用户名称",
          buyerAddress: "地址",
          isTakeout: "外卖配送",
          buyerPhone: "联系方式",
          //productIcon: "图片",
          updateTime: "更新时间",
          createTime: "创建时间"
        },
        tableData: [
          /*{orderId ...}*/
        ]
      }
    };
    data.statusList.forEach(v=>{
      data.statusMap[v.value]= v.label;
    })
    return data;
  },
  mounted() {
    this.$('.jquery-scrollbar-element').scrollbar();
    this.getTableInfo();
  },
  methods: {
    updateScrollbar(row,rows){
      this.$nextTick(()=>{
        if(rows.length==0||row===true){
          this.$('.table-div').updateScrollbars(true)
        }
      });
    },
    changeSearchPage(page_num){
      this.searchInfo.page_num=page_num;
      this.getSearchInfo(true);
    },
    changeSearchPageSize(page_size){
      this.searchInfo.page_size=page_size;
      this.getSearchInfo(true);
    },
    changePage(page_num){
      this.tableInfo.page_num=page_num;
      this.getTableInfo();
    },
    changePageSize(page_size){
      this.tableInfo.page_size=page_size;
      this.getTableInfo();
    },
    updateInfo(){
      //console.log(this.activeTab);
      if(this.activeTab=='menuList'){
        this.getTableInfo()
      }else{
        this.getSearchInfo(true);
      }
    },
    getTableInfo(isSearch=false) {
      let tb = !isSearch ? this.tableInfo : this.searchInfo,param={};
      if(isSearch){
        param = {
          keyword: this.searchInfo.keyword,
          status: this.searchInfo.orderStatus
        }
      }
      //获取商品列表信息
      axios
        .get("/seller/order/list", {
          page_num: tb.page_num,
          page_size: tb.page_size,
          ...param
        })
        .then(res => {
          if (res && res.code == 1 && res.data) {
            res.data.tableData.forEach(v=>{
              v.isTakeout = v.takeout == 0 ? '堂食' : '外卖';
              v.orderStatusStr = this.statusMap[v.orderStatus];
            })
            if(isSearch){
              this.searchInfo = Object.assign(this.searchInfo,res.data);
            }else{
              this.tableInfo = Object.assign(this.tableInfo, res.data);
            }
          }
        });
    },
    getSearchInfo(cacheKeyword=false) {
      if(!cacheKeyword){
        this.searchInfo.keyword = this.keyword;
        this.searchInfo.orderStatus = this.orderStatus;
      }
      this.activeTab = 'searchResult';
      this.getTableInfo(true);
    },
    //
    changeStatus(index,act) {
      let map = this.actionMap[act];
      let url = map.url;
      let action = map.tips;

      //判断是在搜索页还是列表页
      let table = this.activeTab == 'menuList' ? this.tableInfo : this.searchInfo;
      let orderId = table.tableData[index].orderId;
      axios.get(url,{orderId}).then((res)=>{
        if(res&&res.code==1){
          this.$message({message: action+'成功',type: 'success'});
          let index = -1;
          table.tableData.some((v,i)=>{
            if(v.orderId==orderId){
              index =i;
              return true;
            }
          });
          let status = map.status;
          if(index>=0){
            table.tableData[index].orderStatus=status;
            table.tableData[index].orderStatusStr=this.statusMap[status];
          }
        }else{
          this.$alert(res.tips,action+"失败",{confirmButtonText: '确定'});
        }
      })
    }
  }
};
</script>

