import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import OrderManage from '../components/OrderManage.vue'
import ProductManage from '../components/ProductManage.vue'
import StoreProfile from '../components/StoreProfile.vue'
import AccountManage from '../components/AccountManage.vue'
import PersonProfile from '../components/PersonProfile.vue'
import TableManage from '../components/TableManage.vue'
import BannerPicture from '../components/BannerPicture.vue'

Vue.use(VueRouter)

const routes = [{
		path: '/',
		component: Home,
		children: [{
			path: 'OrderManage',
			component: OrderManage
		},{
			path: 'ProductManage',
			component: ProductManage
		},{
			path: 'StoreProfile',
			component: StoreProfile
		},{
			path: 'AccountManage',
			component: AccountManage
		},{
			path: 'PersonProfile',
			component: PersonProfile
		},{
			path: 'TableManage',
			component: TableManage
		},{
			path: 'BannerPicture',
			component: BannerPicture
		},{
			path: '',
			redirect: 'OrderManage'
		}]
	},
	{
		path: '/login',
		name: 'Login',
		// route level code-splitting
		// this generates a separate chunk (about.[hash].js) for this route
		// which is lazy-loaded when the route is visited.
		component: Login
	},
	{
		path: '*',
		redirect: '/'
	}
]

const router = new VueRouter({
	routes
})

export default router
