import axios from 'axios';
import QS from 'qs';
import router from '../router/index.js'
import {Message} from 'element-ui'
import errorCode from '../common/errorCode.js'

//请求超时
axios.defaults.timeout = 20000;
//设置post请求头 (设置post请求格式，以及编码)
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
//设置基础URL地址，往后的请求都是带上该地址
//axios.defaults.baseURL = "/exam-manage";

//添加响应拦截器
axios.interceptors.response.use(function (response) {
    let data = response.data;
    if(data && data.code==errorCode.UNAUTHORIZED){
        Message({
            message: '登录已失效，请重新登录',
            showClose: true,
            onClose(){
                router.push({
                    path: "/login",
                    query: {
                        from: router.currentRoute.path
                    }
                });
            }
        })
    }
    return response;
  }, function (error) {
    // 对响应错误做点什么
    return Promise.reject(error);
  });

// 封装get请求
const get = (url, params,config={}) => {
	return new Promise((resolve, reject) => {
		axios.get(url, {
            params: params,
            ...config
		}).then(res => {
			//成功回调,将返回值返回给调用者
			resolve(res.data);
		}).catch(err => {
			reject(err.data);
		})
	});
}

// 封装post请求
const post = (url, params,config={}) => {
	return new Promise((resolve, reject) => {
		axios.post(url, QS.stringify(params),config).then(res => {
			//成功回调,将返回值返回给调用者
			resolve(res.data);
		}).catch(err => {
			reject(err.data);
		})
	});
}
export {
    axios
}
export default {
	get,
    post
}
