<template>
  <div class="fit-parent">
    <div class="table-manage-div jquery-scrollbar-element scrollbar-macosx">
      <div class="padding24">
        <div class="title-div">
          <h2 class="title-text">
            <span>桌台管理</span>
          </h2>
        </div>
        <div class="accounts-div">
          <div class="tools-div">
            <el-form :inline="true" :model="searchInfo" class="demo-form-inline">
              <el-form-item label="搜索记录:">
                <el-input
                  size="small"
                  class="search-input"
                  placeholder="请输入ID/别名/桌号"
                  prefix-icon="el-icon-search"
                  @keydown.enter.native="getSearchInfo()"
                  v-model="keyword"
                ></el-input>
              </el-form-item>
              <el-form-item>
                <el-button
                  size="small"
                  plain
                  type="primary"
                  @click="getSearchInfo()"
                  icon="el-icon-search"
                >查询</el-button>
              </el-form-item>
              <el-form-item>
                <el-button
                  size="small"
                  plain
                  @click="dialog.addTableVisble=true"
                  icon="el-icon-plus"
                >添加桌台</el-button>
              </el-form-item>
            </el-form>
          </div>
          <div class="table-info-list">
            <el-tabs type="border-card" v-model="activeTab" @tab-click="updateInfo">
              <el-tab-pane label="桌台列表" name="menuList">
                <i class="el-icon-refresh refresh-table" @click="getTableInfo()"></i>
                <div class="table-div jquery-scrollbar-element scrollbar-macosx">
                  <div class="inline-block">
                    <el-table :data="tableInfo.tableData" border style="width: 100%">
                      <el-table-column label="ID" prop="id"></el-table-column>
                      <el-table-column label="别名" prop="tableName"></el-table-column>
                      <el-table-column label="桌号" prop="tableNum"></el-table-column>
                      <el-table-column label="二维码">
                        <template slot-scope="scope">
                          <el-image v-if="scope.row['qrcodeUrl']" style="width: 50px; height: 50px" class="pointer-cursor" 
                          @click.native="previewQrcode(scope.$index)" fit="cover" :src="scope.row['qrcodeUrl']"></el-image>
                          <span v-else>暂无，点击右边生成</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="创建时间" prop="createTime"></el-table-column>
                      <el-table-column label="更新时间" prop="updateTime"></el-table-column>
                      <el-table-column label="操作" width="180">
                        <template slot-scope="scope">
                          <el-button
                            type="primary"
                            icon="el-icon-thumb"
                            circle
                            title="生成二维码"
                            @click="generateQrcode(scope.$index)"
                            size="small">
                          </el-button>
                          <el-button
                            type="success"
                            icon="el-icon-download"
                            circle
                            title="下载"
                            @click="downloaQrcode(scope.$index)"
                            size="small">
                          </el-button>
                          <el-button
                            type="primary"
                            icon="el-icon-edit"
                            circle
                            title="编辑"
                            @click="showEditTable(scope.$index)"
                            size="small"
                          ></el-button>
                          <el-button
                            type="danger"
                            icon="el-icon-delete"
                            circle
                            title="删除账号"
                            @click="deleteTable(scope.$index)"
                            size="small"
                          ></el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div>
                </div>
                <div class="pager-div">
                  <el-pagination
                    @size-change="changePageSize"
                    @current-change="changePage"
                    :current-page="tableInfo.page_num"
                    :page-sizes="[6, 8, 10, 15, 20, 30]"
                    :page-size="tableInfo.page_size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="tableInfo.total_num"
                    :pager-count="5"
                    size="small"
                  ></el-pagination>
                </div>
              </el-tab-pane>
              <!-- <el-tab-pane label="菜单预览" name="menuPreview">菜单预览</el-tab-pane> -->
              <el-tab-pane label="搜索结果" name="searchResult">
                <i class="el-icon-refresh refresh-table" @click="getSearchInfo(true)"></i>
                <div class="table-div jquery-scrollbar-element scrollbar-macosx">
                  <div class="inline-block">
                    <el-table :data="searchInfo.tableData" style="width: 100%" border>
                      <el-table-column label="ID" prop="id"></el-table-column>
                      <el-table-column label="别名" prop="tableName"></el-table-column>
                      <el-table-column label="桌号" prop="tableNum"></el-table-column>
                      <el-table-column label="二维码">
                        <template slot-scope="scope">
                          <el-image v-if="scope.row['qrcodeUrl']" style="width: 50px; height: 50px" class="pointer-cursor" 
                          @click.native="previewQrcode(scope.$index)" fit="cover" :src="scope.row['qrcodeUrl']"></el-image>
                          <span v-else>暂无，点击右边生成</span>
                        </template>
                      </el-table-column>
                      <el-table-column label="创建时间" prop="createTime"></el-table-column>
                      <el-table-column label="更新时间" prop="updateTime"></el-table-column>
                      <el-table-column label="操作" width="180">
                        <template slot-scope="scope">
                          <el-button
                            type="primary"
                            icon="el-icon-thumb"
                            circle
                            title="生成二维码"
                            @click="generateQrcode(scope.$index)"
                            size="small">
                          </el-button>
                          <el-button
                            type="success"
                            icon="el-icon-download"
                            circle
                            title="下载"
                            @click="downloaQrcode(scope.$index)"
                            size="small">
                          </el-button>
                          <el-button
                            type="primary"
                            icon="el-icon-edit"
                            circle
                            title="编辑"
                            @click="showEditTable(scope.$index)"
                            size="small"
                          ></el-button>
                          <el-button
                            type="danger"
                            icon="el-icon-delete"
                            circle
                            title="删除账号"
                            @click="deleteTable(scope.$index)"
                            size="small"
                          ></el-button>
                        </template>
                      </el-table-column>
                    </el-table>
                  </div>
                </div>
                <div class="pager-div">
                  <el-pagination
                    @size-change="changeSearchPageSize"
                    @current-change="changeSearchPage"
                    :current-page="searchInfo.page_num"
                    :page-sizes="[6, 8, 10, 15, 20, 30]"
                    :page-size="searchInfo.page_size"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="searchInfo.total_num"
                    :pager-count="5"
                    size="small"
                  ></el-pagination>
                </div>
              </el-tab-pane>
            </el-tabs>
          </div>
        </div>
        <el-dialog title="添加桌台" :visible.sync="dialog.addTableVisble" :modal-append-to-body="false">
          <el-form :model="dialog.newTable" :rules="dialog.rules" ref="addTableForm">
            <el-form-item label="桌号" :label-width="'120px'" prop="tableNum">
              <el-input v-model="dialog.newTable.tableNum" placeholder="请输入唯一桌号" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="别名" :label-width="'120px'" prop="tableName">
              <el-input v-model="dialog.newTable.tableName" placeholder="请输入别名" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog.addTableVisble = false">取 消</el-button>
            <el-button type="primary" @click="addTable()">确 定</el-button>
          </div>
        </el-dialog>
        <el-dialog title="编辑桌台" :visible.sync="dialog.editTableVisble" :modal-append-to-body="false">
          <el-form :model="dialog.table" :rules="dialog.rules" ref="editTableForm">
            <el-form-item label="桌号" :label-width="'120px'" prop="tableNum">
              <el-input v-model="dialog.table.tableNum" placeholder="请输入唯一桌号" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="别名" :label-width="'120px'" prop="tableName">
              <el-input v-model="dialog.table.tableName" placeholder="请输入别名" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialog.editTableVisble = false">取 消</el-button>
            <el-button type="primary" @click="editTable()">确 定</el-button>
          </div>
        </el-dialog>
      </div>
    </div>
    <el-dialog :visible.sync="dialog.previewImageVisible" :modal-append-to-body="false">
      <el-image style="width: 400px; height: 400px" fit="cover" :src="dialog.dialogImageUrl"></el-image>
    </el-dialog>
    <a href="javascript:;" download class="unvisible" id="downloadAction"></a>
  </div>
</template>

<script>
import axios from "../common/http.js";
export default {
  data() {
    let data = {
      dialog: {
        dialogImageUrl: '',
        previewImageVisible: false,
        addTableVisble: false,
        editTableVisble: false,
        newTable: {
          tableNum: '',
          tableName: ''
        },
        table: {
          tableNum: '',
          tableName: ''
        },
        rules: {
          tableNum: [
            {
              validator: (rule, value, callback) => {
                if (!value) {
                  callback(new Error("请输入唯一桌号"));
                } else {
                  callback();
                }
              },
              trigger: "blur"
            }
          ],
          tableName: [
            {
              validator: (rule, value, callback) => {
                if (!value) {
                  callback(new Error("请输入别名"));
                } else {
                  callback();
                }
              },
              trigger: "blur"
            }
          ]
        }
      },
      activeTab: "menuList",
      searchInfo: {
        keyword: "",
        page_num: 1,
        page_size: 6,
        total_num: 1,
        tableData: []
      },
      keyword: "",
      tableInfo: {
        page_num: 1,
        page_size: 6,
        total_num: 1,
        tableData: [
          /*{id ...}*/
        ]
      }
    };
    return data;
  },
  mounted() {
    this.$(".jquery-scrollbar-element").scrollbar();
    this.getTableInfo();
  },
  methods: {
    generateQrcode(index){
      let table = this.activeTab == 'menuList' ? this.tableInfo : this.searchInfo;
      let tableNum = table.tableData[index].tableNum;
      //目前使用后台的createQrcode接口根据字符串生成一个二维码，后续小程序上线后可以改为使用generateQrcode接口生成小程序码
      axios.get('/storeTable/createQrcode',{tableNum}).then((res)=>{
        if (res && res.code == 1) {
          this.$message({ message: "生成二维码成功", type: "success" });
          this.updateInfo();
        } else {
          this.$alert(res.tips, "生成二维码失败", {
            confirmButtonText: "确定"
          });
        }
      });
    },
    downloaQrcode(index){
      let table = this.activeTab == 'menuList' ? this.tableInfo : this.searchInfo;
      let qrcodeUrl = table.tableData[index].qrcodeUrl;
      this.$('#downloadAction').attr('download',qrcodeUrl).attr("target","_blank").attr("href",qrcodeUrl).get(0).click();
    },
    previewQrcode(index){
      let table = this.activeTab == 'menuList' ? this.tableInfo : this.searchInfo;
      let qrcodeUrl = table.tableData[index].qrcodeUrl;
      this.dialog.dialogImageUrl = qrcodeUrl;
      this.dialog.previewImageVisible = true;
    },
    changeSearchPage(page_num) {
      this.searchInfo.page_num = page_num;
      this.getSearchInfo(true);
    },
    changeSearchPageSize(page_size) {
      this.searchInfo.page_size = page_size;
      this.getSearchInfo(true);
    },
    changePage(page_num) {
      this.tableInfo.page_num = page_num;
      this.getTableInfo();
    },
    changePageSize(page_size) {
      this.tableInfo.page_size = page_size;
      this.getTableInfo();
    },
    updateInfo() {
      if (this.activeTab == "menuList") {
        this.getTableInfo();
      } else {
        this.getSearchInfo(true);
      }
    },
    getTableInfo(isSearch = false) {
      let tb = !isSearch ? this.tableInfo : this.searchInfo,
        param = {};
      if (isSearch) {
        param = {
          keyword: this.searchInfo.keyword
        };
      }
      //获取列表信息
      axios
        .get("/storeTable/list", {
          page_num: tb.page_num,
          page_size: tb.page_size,
          ...param
        })
        .then(res => {
          if (res && res.code == 1 && res.data) {
            if (isSearch) {
              this.searchInfo = Object.assign(this.searchInfo, res.data);
            } else {
              this.tableInfo = Object.assign(this.tableInfo, res.data);
            }
          }
        });
    },
    getSearchInfo(cacheKeyword = false) {
      if (!cacheKeyword) {
        this.searchInfo.keyword = this.keyword;
      }
      this.activeTab = "searchResult";
      this.getTableInfo(true);
    },
    //
    showaddTable(){
      this.dialog.addTableVisble = true;
    },
    showEditTable(index) {
      let table = this.activeTab == 'menuList' ? this.tableInfo : this.searchInfo;
      this.dialog.table = {...table.tableData[index]};
      this.dialog.editTableVisble = true;

    },
    addTable(){
      this.dialog.addTableVisble = false;
      this.editTable(true);
    },
    editTable(isAdd=false){
      if(!isAdd) this.dialog.editTableVisble = false;
      let act = isAdd ? this.dialog.newTable : this.dialog.table;
      let action = isAdd ? '添加' : '编辑';
      let ref = isAdd ? this.$refs["addTableForm"] : this.$refs["editTableForm"];
      let table = {...act};
      delete table.updateTime;
      delete table.createTime;
      ref.validate(valid => {
        if(valid){
          axios.post('/storeTable/save',table).then(res=>{
            if (res && res.code == 1) {
              this.$message({ message: action+"桌台成功", type: "success" });
              if(isAdd){
                ref.resetFields();
              }
              this.updateInfo();
            } else {
              this.$alert(res.tips, action+"桌台失败", {
                confirmButtonText: "确定"
              });
            }
          });
        } else {
          this.$alert("信息填写有误", "未提交", {
            confirmButtonText: "确定"
          });
          return false;
        }
      });
    },
    deleteTable(index) {
      let table = this.activeTab == 'menuList' ? this.tableInfo : this.searchInfo;
      let id = table.tableData[index].id;
      this.$confirm('此操作将永久删除该桌台, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios.post('/storeTable/delete',{id}).then((res)=>{
          if(res&&res.code==1){
            this.$message({message: '删除桌台成功',type: 'success'});
            this.updateInfo();
          }else{
            this.$alert(res.tips,"删除桌台失败",{confirmButtonText: '确定'})
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });          
      });
    }
  }
};
</script>

